.App {
  min-height: 100vh;
  text-align: center;
  background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 8vh;
  background-color: rgb(73, 79, 82);
  color: white;
}

.nav-links {
  display: flex;
  width: 50%;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

h1 {
  color: white;
  font-size: 50px;
}

.search-form {
  min-height: 10vh;
  display: ruby;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}
.search-button {
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
}

.phrases {
  font-size: 40px;
  color: white;
  list-style-type: none;
  font-family: "Poppins", sans-serif;
}
.listpage {
  list-style-type: none;
  font-size: 35px;
  color: black;
}

.box {
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(71, 71, 71);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: white;
  align-items: center;
  min-width: 40%;
}
